.App {
  width: 100%;
  text-align: center;
}

.navigation {
  width: 100%;
  height: 64px;
  background-color: #483d8b;
  font-family: 'Source Code Pro', 'monospace';
  font-size: 16px;
  font-weight: 400;
  color: #fff;
}

.navigation-sub {
  max-width: 1024px;
  margin: auto;
  height: 100%;
}

.navigation-sub p {
  float: left;
  text-align: left;
  padding: 16px;
  margin: 0;
}

.navigation-sub ul {
  display: inline;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.navigation-sub li {
  float: right;
}

.navigation-sub .item {
  display: block;
  padding: 16px;
  padding-top: 20px;
  height: 28px;
  color: #fff;
  text-decoration: none;
  transition: 0.1s;
  background-color: #483d8b;
}

.navigation-sub .item:hover {
  background-color: #6f64b8;
  transition: 0.1s;
}

.navigation-sub .active,
.navigation-sub .active:hover {
  background-color: #2e2950;
}

.fade-enter {
  opacity: 0.01;
}
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}
.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

div.transition-group {
  position: relative;
}
section.route-section {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}